<template>
  <Card class="card">
    <Breadcrumb>
      <BreadcrumbItem>货主中心</BreadcrumbItem>
      <BreadcrumbItem>修改货主信息</BreadcrumbItem>
    </Breadcrumb>
    <div class="box-container">
      <Collapse v-model="value1" accordion>
        <Panel name="1">
           <span>货主信息</span>
          <Row slot="content" class="row">
              <Col span="3">货主</Col>
              <Col span="10">{{result.companyName}}</Col>
          </Row>

           <Row slot="content" class="row">
              <Col span="3">货主官网网址</Col>
              <Col span="8">
                <Input style="width:220px" placeholder="请输入货主官网网址" v-model="offUrl" />
              </Col>
              <Col span="3">官网招标网址</Col>
              <Col span="8">
                <Input style="width:220px" placeholder="请输入官网招标网址" v-model="offsUrl"  />
             </Col>
          </Row>
           <Row slot="content" class="row" style="margin:10px 0">
              <Col span="3">货主简介</Col>
              <Col span="8">
                <Input style="width:220px" type="textarea" :rows="4"
                    v-model="note"
                  placeholder="请输入货主简介"/>
              </Col>
          </Row>
          <Row slot="content" class="row">
            <Divider/>
          </Row>
          <Row slot="content" class="row">
             <h3>业务状况</h3>
          </Row>
          <Row slot="content" class="row" v-for="(item,index) in preLine" :key="index">
              <Col span="3"> <span v-if="!index">偏好航线</span> <span v-else>&nbsp</span> </Col>
              <Col span="15">
                <Input style="width:120px" placeholder="起始航线" v-model="item.starLine"/> <span style="margin:0 10px;">到</span>
                <Input style="width:120px" placeholder="结束航线" v-model="item.endLine"/>
                <Icon type="ios-add-circle"  class="icons" @click="addFun(0,index)" v-if="!index"/>
                <Icon type="ios-remove-circle" class="icons"  @click="minusFun(0,index)" v-else/>
              </Col>
          </Row>
          <Row slot="content" class="row"  v-for="(item,index) in preShip" :key="index+100">
              <Col span="3"> <span v-if="!index">偏好船型</span> <span v-else>&nbsp</span> </Col>
              <Col span="15">
                  <Input style="width:220px" placeholder="偏好船型" v-model="item.shipType" />
                  <Icon type="ios-add-circle"  class="icons" @click="addFun(1,index)" v-if="!index"/>
                  <Icon type="ios-remove-circle" class="icons"  @click="minusFun(1,index)" v-else/>
               </Col>
          </Row>
          <Row slot="content" class="row"  v-for="(item,index) in preGoods" :key="index+1000">
              <Col span="3"> <span v-if="!index">偏好货种</span> <span v-else>&nbsp</span> </Col>
              <Col span="15">
                     <Input style="width:220px" placeholder="偏好货种" v-model="item.goodsType" />
                     <Icon type="ios-add-circle"  class="icons" @click="addFun(2,index)" v-if="!index"/>
                     <Icon type="ios-remove-circle" class="icons"  @click="minusFun(2,index)" v-else/>
               </Col>
          </Row>
          <Row slot="content" class="row">
              <Divider/>
          </Row>
           <Row slot="content" class="row">
              <Button type="primary" @click="save">保存</Button>
          </Row>
        </Panel>
      </Collapse>
    </div>
  </Card>
</template>
<script>
   export default {
        data () {
            return {
                value1: '1',
                value2:'2',
                result:'',
                offUrl:'',
                offsUrl:"",
                note:'',
                preLine:[
                    {
                        starLine:'',
                        endLine:''
                    }
                ],
                  preShip:[
                    {
                        shipType:'',
                    }
                ],
                  preGoods:[
                    {
                        goodsType:'',
                    }
                ],
            }
        },
        methods:{
         async  huozhuDetail(){
              let data  = {
                code:this.$route.query.code
              }
             try{

                   let res = await this.$http.huozhuDetail(data)
                   if(0==res.data.status ){
                     this.result = res.data.result
                        this.offUrl =  res.data.result.companyOfficialUrl
                        this.offsUrl =  res.data.result.companyBidUrl
                        this.note =  res.data.result.companyInfo
                        if(res.data.result.preferCargo){
                            let cargo = res.data.result.preferCargo.split(',')
                            this.preGoods = []
                            for(let i in cargo){
                                 this.preGoods.push({
                                     goodsType:cargo[i]
                                 })
                            }
                        }
                        if(res.data.result.preferShipType){
                            let preferShipType = res.data.result.preferShipType.split(',')
                            this.preShip = []
                            for(let i in preferShipType){
                                 this.preShip.push({
                                     shipType:preferShipType[i]
                                 })
                            }
                        }
                        if(res.data.result.preferVoyage){
                            let preferVoyage = res.data.result.preferVoyage.split(',')
                            this.preLine = []
                            for(let i in preferVoyage){
                                 this.preLine.push({
                                    starLine:preferVoyage[i].split('-')[0],
                                    endLine:preferVoyage[i].split('-')[1]
                                 })
                            }
                        }

                   }
              }
              catch(err){
                // this.$Notice.error({
                //   desc:'服务器错误,请稍后重试.'
                // })
                throw Error(err)

              }
            },
         //    添加
           addFun(type,index){
            switch(type){
              case 0:
                  this.preLine.push({
                        starLine:'',
                        endLine:''
                    })
                  break
              case 1:
                    this.preShip.push({
                        shipType:'',
                    })
                  break
              case 2:
                 this.preGoods.push({
                     goodsType:''
                 })
                  break
            }
           },
        //    减少
           minusFun(type,index){
             switch(type){
              case 0:
                  this.preLine.splice(index,1)
                  break
              case 1:
                  this.preShip.splice(index,1)
                  break
              case 2:
                  this.preGoods.splice(index,1)
                  break
            }
           },
          async save(){
              let preferShipType = []
              for(let i in this.preShip){
                  preferShipType.push(this.preShip[i].shipType)
              }
              let preferCargo = []
              for(let i in this.preGoods){
                  preferCargo.push(this.preGoods[i].goodsType)
              }
              let preferVoyage = []
              for(let i in this.preLine){
                  preferVoyage.push(this.preLine[i].starLine+'-'+this.preLine[i].endLine)
              }
              let data = {
                 cargoOwnerCompanyCode:this.$route.query.code,
                 companyBidUrl:this.offsUrl,
                 companyOfficialUrl:this.offUrl,
                 info:this.note,
                 preferShipType:preferShipType.join(','),
                 preferCargo:preferCargo.join(','),
                 preferVoyage:preferVoyage.join(','),
              }
            try{

                let res = await this.$http.updatehzInfo(data)
                if(!res.data.status){
                    this.$Notice.success({
                        desc:"保存成功"
                    })
                    setTimeout(()=>{
                        const {href} = this.$router.resolve({
                            path:'/hzCenter/hzdetail',
                            query:{
                                code:this.$route.query.code
                            }
                         });
                         window.open(href, '_blank')
                    },1300)
                }else{
                     this.$Notice.error({
                        desc:res.data.msg
                    })
                    return false
                }
            }
            catch(err){
                this.$Notice.error({
                    desc:'服务器错误,请稍后重试.'
                })
                console.log(err)

            }
           }
        },
        created(){
          this.huozhuDetail()
        }
    }
</script>
<style lang="scss" scoped>
.table-row{
  margin: 20px 0px;
}
.ivu-row{
    padding: 0 30px;
    margin: 10px 0;
}
.table, tr ,td{
  border: 1px solid #e4e4ee;
  border-collapse: collapse;
  padding: 8px 30px;
  text-align: center;
}
.icons{
    font-size: 22px;
    cursor: pointer;
}
</style>



